html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Nunito", sans-serif;
  font-weight: 400;
  color: var(--color-gray-900);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

p {
  font-size: 16px;
  line-height: 22px;
  margin-top: 0;

  &:not(:last-child) {
    margin-bottom: 15px;
  }
}

svg {
  width: 20px;
  height: 20px;
}

.site-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 100vh;
}

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1440px;
  width: 100%;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.text__block {
  ul {
    margin: 0 0 25px 0;
    padding: 0 0 0 15px;
    list-style: none;

    li {
        padding-left: 15px;
        position: relative;
        font-size: 16px;
        line-height: 22px;

        &::before {
            content: '';
            width: 5px;
            height: 5px;
            position: absolute;
            left: 0;
            display: inline-block;
            background-color: var(--corporate-v-2);
            border-radius: 50%;
            top: 9px;
        }

        &:not(:last-child) {
            margin-bottom: 5px;
        }
    }
  }
}

.main__indent {
  display: block;
  width: 100%;
  height: 130px;

  @include before('xxl') {
    height: 100px;
  }

  @include before('lg') {
    height: 80px;
  }
}


.main__button {
  background-color: var(--corporate-v-2);
  border-radius: 4px;
  border: 2px solid var(--corporate-v-2);
  padding: 15px 25px;
  font-size: 16px;
  font-weight: 700;
  color: var(--color-gray-100);
  transition: all 0.2s;

  &:hover, &:focus, &:active, &.active {
    background-color: var(--color-gray-100);
    color: var(--corporate-v-2);
    transition: all 0.2s;
  }
}